$font-family: "Gilroy", sans-serif;
$color-brand: #ff4419;
$color-brand-2: #ff7400;

a {
     color: inherit;
     text-decoration: none !important;
}

a,
p,
h1,
h2,
h3,
h4,
h5,
h6,
li,
.h1,
.h2,
.h3,
.h4,
.h5,
.h6 {
     font-family: "Gilroy", sans-serif !important;
}

//Botones
@import "./botones.scss";

// Men√∫  //
.menu-top {
     border-bottom: 1px solid $color-brand;
     .logo-yaguar {
          max-height: 51px;
          width: auto;
          @media (max-width: 991px) {
               max-height: 40px;
               padding: 5px;
          }
     }
     .nav-link {
          font-family: $font-family;
          font-size: 16px;
          font-weight: 400;
     }
     &-collapse {
          &.navbar-collapse {
               flex-grow: unset;
          }
     }
     .collapse-hidden {
          @media (max-width: 991px) {
               &_pc {
                    display: block !important;
               }
               &_movil {
                    display: none !important;
               }
          }
          @media (min-width: 992px) {
               &_pc {
                    display: none !important;
               }
               &_movil {
                    display: flex !important;
               }
          }
     }
}

header {
     .py-header {
          padding: 100px 0;
          @media (max-width: 991px) {
               padding: 80px 0 40px 0;
          }
     }
     .header {
          &-titulo {
               font-size: 60px;
               font-weight: bold;
               line-height: 68px;
               font-family: $font-family !important;
               text-align: left;
          }
          &-descripcion {
               font-size: 20px;
               font-family: $font-family;
          }
          &-btn {
               width: 198px;
               font-size: 20px;
          }
          &-imagen {
               width: 100%;
               &-hidden_pc {
                    display: none;
               }
               &-hidden_movil {
                    display: block;
               }
          }

          @media (min-width: 768px) and (max-width: 991px) {
               &-titulo {
                    font-size: 36px;
                    font-weight: bold;
                    line-height: 46px;
                    font-family: $font-family !important;
                    text-align: center;
               }
               &-descripcion {
                    font-size: 20px;
                    line-height: 30px;
                    font-family: $font-family;
                    text-align: center;
               }
               &-btn {
                    width: 100%;
                    font-family: $font-family;
                    font-size: 20px;
               }
               &-imagen {
                    width: 98%;
                    &-hidden_movil {
                         display: none;
                    }
                    &-hidden_pc {
                         display: block;
                         padding: 25px 0;
                    }
               }
          }
          @media (max-width: 992px) and (max-width: 1199px) {
               &-titulo {
                    font-size: 36px;
                    font-weight: bold;
                    line-height: 46px;
                    font-family: $font-family !important;
                    text-align: center;
               }
               &-descripcion {
                    font-size: 22px;
                    line-height: 30px;
                    font-family: $font-family;
                    text-align: center;
               }
               &-btn {
                    width: 100%;
                    font-family: $font-family;
                    font-size: 22px;
               }
               &-imagen {
                    width: 98%;
                    &-hidden_movil {
                         display: none;
                    }
                    &-hidden_pc {
                         display: block;
                         padding: 25px 0;
                    }
               }
          }
     }
}

.subtitle {
     line-height: 50px;
     font-size: 46px;
     text-align: center;
     @media (max-width: 991px) {
     }
}

//Carousel

.carousel {
     width: 100%;
     margin: 0px auto;
}
.slick-slide {
     margin: 10px;
}

.slick-slide img {
     width: 80%;
     padding: 0 10px;
}
@media (max-width: 991px) {
     .slick-slide img {
          width: 100%;
          padding: 0 10px;
     }
}

.slick-track {
     display: flex;
     align-items: center;
}

