.container-empezar {
     display: grid;
     grid-template-columns: repeat(4, 1fr);
     grid-template-rows: repeat(3, auto);
     border-radius: 10px;
     overflow: hidden;
     &_cta {
          grid-area: 2/1/3/3;
          z-index: 2;
          margin-left: 50px;
     }
     &_logo {
          height: 34px;
     }
     &_description {
          font-size: 60px;
          font-weight: bold;
          line-height: 70px;
          margin-bottom: 34px;
     }
     @media (max-width: 991px) {
          &_logo {
               height: 34px;
               left: 50%;
               position: relative;
               transform: translateX(-50%);
          }
          &_description {
               font-size: 44px;
               font-weight: bold;
               line-height: 55px;
               margin-bottom: 34px;
               text-align: center;
               width: 70%;
               left: 50%;
               position: absolute;
               transform: translateX(-50%);
          }
          &_cta {
               grid-area: 1/1/4/5;
               z-index: 2;
               position: relative;
               margin-left: unset;
               .btn_naranja {
                    position: absolute;
                    bottom: 10px;
                    width: 90%;
                    left: 50%;
                    position: absolute;
                    transform: translateX(-50%);
               }
          }
     }
     &_img {
          z-index: 0;
          grid-area: 1/1/4/5;
          justify-self: end;
          border-radius: 0 10px 10px 0;
          &_pc {
               grid-area: 1 / 1 / 4 / 5;
               height: 100%;
               display: block;
               justify-self: end;
               @media (max-width: 991px) {
                    display: none;
               }
          }
          &_movil {
               grid-area: 1 / 1 / 4 / 5;
               justify-self: end;
               height: 100%;
               z-index: 0;
               border-radius: 0 10px 10px 0;
               display: none;
               @media (max-width: 991px) {
                    display: block;
                    width: 100%;
                    height: auto;
                    align-self: end;
                    justify-self: stretch;
               }
               @media (min-width: 768px) and (max-width: 991px) {
                    display: block;
                    width: 100%;
                    height: auto;
                    align-self: end;
                    justify-self: stretch;
               }
               @media (max-width: 992px) and (max-width: 1199px) {
                    display: block;
                    width: 100%;
                    height: auto;
                    align-self: end;
                    justify-self: stretch;
               }
          }
     }
     &_overlay {
          z-index: 1;
          grid-area: 1/1/4/4;
          background: linear-gradient(90deg, white 45%, #fff0);
          @media (max-width: 991px) {
               grid-area: 1/1/4/5;
               background: linear-gradient(
                    180deg,
                    rgba(255, 255, 255, 0.84) 5%,
                    rgba(255, 255, 255, 0.0392156863) 34%,
                    rgba(255, 255, 255, 0)
               );
          }
     }
}

//Footer
footer {
     width: 100%;
     border-top: 1px solid $color-brand;
     padding-bottom: 20px;
     .footer {
          display: flex;
          justify-content: space-between;
          flex-direction: row;
          ul {
               display: flex;
               flex-direction: row;
               padding-left: 0px;
          }
          li:not(:last-child) {
               margin-right: 15px;
          }
          li {
               list-style: none;
               font-size: 14px;
               font-weight: 400;
               letter-spacing: 0.05em;
               img {
                    margin-right: 5px;
                    height: 20px;
               }
          }
     }
     @media (max-width: 991px) {
          .footer {
               flex-direction: column;
               padding: 25px 33px;
               ul {
                    display: flex;
                    flex-direction: row;
                    padding-left: 0px;
               }
               li {
                    list-style: none;
               }
               .lista_left {
                    flex-direction: column;
                    li {
                         font-size: 16px;
                         margin-bottom: 8px;
                    }
               }
          }
     }
}

