$font-family: "Gilroy", sans-serif;
$color-brand: #ff4419;

.btn_naranja {
     background-color: $color-brand;
     color: white;
     font-family: $font-family;
     font-size: 18px;
     padding: 10px 0;
     min-width: 200px;
     @media (min-width: 768px) and (max-width: 991px) {
          font-size: 20px;
     }
     @media (max-width: 992px) and (max-width: 1199px) {
          font-size: 22px;
     }
     &:hover {
          background-color: $color-brand;
          color: white;
     }
}

.btn_whatsapp{
    background: linear-gradient(180deg, #60D66A 42%, #20B038 100%);
    color: white;
    padding: 10px 20px;
    border-radius: 6px;
}

